  data() {
    return {
      deferredPrompt: null,
      dialog: false,
      about: false,
      info: false,
      hideTitle: true,
      isDownloadPage: true,
      isInstalled: false,
      colors: [
        "indigo",
        "warning",
        "pink darken-2",
        "red lighten-1",
        "deep-purple accent-4",
      ],
      slides: [
        "https://telegram.org/file/811140358/152f/5p8laD7lkz0.123741/df21e01f6bad0e02e3",
        "https://telegram.org/file/811140609/1cc8/sA4zGPiPQog.106018/b66b36041eb3004fd4",
